import char_to_id from "./char_to_id";
import l_vocab from "./l_vocab";
import i_vocab from "./i_vocab";
import l_idf from "./l_idf";
import i_idf from "./i_idf"

const vec_size = Object.keys(char_to_id).length;
export function make_count_vec(string) {
  let vec = new Array(vec_size);
  vec.fill(0);
  for (let i = 0; i < string.length; i++) {
    let cid = char_to_id[string[i]];
    vec[cid] += 1;
  }
  return vec;
}

export function make_i_feature_vec(string) {
  let coef = 1;
  if (i_vocab[string]) {
    let idx = i_vocab[string];
    coef *= i_idf[idx];
  }
  let vec = new Array(vec_size);
  vec.fill(0);
  for (let i = 0; i < string.length; i++) {
    let cid = char_to_id[string[i]];
    vec[cid] += coef;
  }
  return vec;
}

export function make_l_feature_vec(string) {
  let coef = 1;
  if (l_vocab[string]) {
    let idx = l_vocab[string];
    coef *= l_idf[idx];
  }
  let vec = new Array(vec_size);
  vec.fill(0);
  for (let i = 0; i < string.length; i++) {
    let cid = char_to_id[string[i]];
    vec[cid] += coef;
  }
  return vec;
}